// External Dependencies
import React from 'react';
import PropTypes from 'prop-types';

// Internal Dependencies
import SignupContainer from './SignupContainer';
import { preRenderFetch } from '../services/forms';

class SignupRetriever extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      preRenderFetchComplete: false
    };
  }
  componentDidMount() {
    const { userID, formID } = this.props;
    preRenderFetch({ userID, formID })
      .then((updates) => {
        this.setState({
          ...this.state,
          preRenderFetchComplete: true,
          ...updates
        });
      })
      .catch(err => console.error(Error(err)));
  }
  render() {
    // strip off props that aren't necessary for SignupContainer to know about
    const { preRenderFetchComplete, name, version, ...state } = this.state;
    if (this.state.preRenderFetchComplete) {
      return (
        <SignupContainer
          {...this.props}
          {...state}
        />
      );
    }
    return (
      <div className="Signup Signup--loading">
        <div className="F" />
      </div>
    );
  }
}
SignupRetriever.propTypes = {
  formID: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.string
  ]),
  userID: PropTypes.string.isRequired
};
SignupRetriever.defaultProps = {
  formID: false
};

export default SignupRetriever;
