// External Dependencies
import database from '../firebase/firebase';

export const preRenderFetch = ({ userID, formID }) => {
  const contactFormLocation = (`/users/${userID}/contactForms/${formID}`);
  return database.ref(contactFormLocation).once('value')
    .then(snapshot => {
      const val = snapshot.val();
      // console.log(val);
      return Promise.resolve(val);
    });
};
