import React from 'react';
import ReactDOM from 'react-dom';

// this isn't a direct depencency, it's included in nove-shared's tree
import 'react-datepicker/dist/react-datepicker.css';

import SignupRetriever from './containers/SignupRetriever';
import './styles/app.sass';
import { version } from '../package.json';

const style = [
  'color: #0099e1',
  'border: 1px solid',
  'padding: 1px 8px 0px 8px',
  'margin: 1px 0 5px -18px',
  'line-height: 18px'
].join(';');

let SignupConfig = {
  // no default config at the moment
  acceptedOtherProps: ['className', 'order', 'production', 'styles', 'types', 'testForm']
};
export default {
  config: (config) => {
    SignupConfig = {
      ...SignupConfig,
      ...config
    };
  },
  widgets: {
    signUp: {
      new: (config = { selector: '#nove_signup' }) => {
        return ({
          render: (args = {}) => new Promise((res, rej) => {
            // strains out and keeps accepted props named in the array below
            const acceptedOtherProps = {};
            SignupConfig.acceptedOtherProps.forEach((prop) => {
              const acceptedOtherProp = args[prop];
              if (acceptedOtherProp !== undefined) {
                acceptedOtherProps[prop] = acceptedOtherProp;
              }
            });
            ReactDOM.render(
              <SignupRetriever
                {...acceptedOtherProps}
                userID={args.userID || config.userID || SignupConfig.userID}
                formID={args.formID || config.formID || SignupConfig.formID}
              />,
              document.querySelector(args.selector || config.selector || SignupConfig.selector)
            );
            console.log(`%c${version} Nove.io Contact Form Loaded`, style);
            res(true);
          }),
          destroy: (args = {}) => new Promise((res, rej) => {
            ReactDOM.unmountComponentAtNode(document.querySelector(args.selector || config.selector || SignupConfig.selector));
            res(true);
          }),
          version
        });
      }
    }
  }
};
