// External Dependencies
import React from 'react';
import PropTypes from 'prop-types';
import database from '../firebase/firebase';

// Internal Dependencies
import { formatFormInput } from '../services/inquiry';
import inquiriesPath from '../services/auth';

// Nove Dependencies
const {
  FORM_ORDER,
  flattenInputs,
  Signup,

  getInputConfig,
  getRenderedComponents,
  getHydratedInputs,
  getStyles,
  getCSS,
  renormalizeInputs
} = require('nove-shared').default;

const testing = process.env.NODE_ENV === 'test';

class SignupContainer extends React.Component {
  constructor(props) {
    super(props);
    // gets any stored values if they exist for this form
    const storedState = (this.props.formID && window.localStorage.getItem(this.props.formID)) || '{}';
    const { inputs: denormalizedInputs } = this.props;
    const hydratedInputs = getHydratedInputs({ inputs: denormalizedInputs });
    const inputs = renormalizeInputs(hydratedInputs);

    this.flattenedInputs = hydratedInputs;

    const defaultValues = this.getDefaultValues();

    const initialState = {
      accordionOpen: false,
      emailError: '',
      formError: '',
      id: '',
      ...defaultValues,
      // used in form submission to retrieve values from state
      inputs,
      phoneError: '',
      signupSuccess: false,
      signupSuccessButton: this.props.signupSuccessButton,
      signupSuccessHeading: this.props.signupSuccessHeading,
      signupSuccessMessage: this.props.signupSuccessMessage,
      userID: this.props.userID,
      windowWidth: 0
    };
    this.state = {
      ...initialState,
      ...JSON.parse(storedState)
    };
  }
  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }
  componentWillReceiveProps = (nextProps) => {
    // it seems to be unresponsive to updates in props.order
    // force update every time there is a change
    const currJSON = JSON.stringify({ a: this.props.inputs });
    const nextJSON = JSON.stringify({ a: nextProps.inputs });
    if (!(currJSON === nextJSON)) {
      const { inputs: denormalizedInputs } = nextProps;
      const inputs = renormalizeInputs(denormalizedInputs);

      this.flattenedInputs = flattenInputs(inputs);
      this.setState({
        ...this.state,
        ...this.getDefaultValues(),
        inputs
      });
    }
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }
  onAmountChange = ({ target }) => {
    const { id, value: amount } = target;
    // starts with digit, any # of digits, allows suffix of one period and 0-2 digits, nothing after
    if (!amount || amount.match(/^\d{1,}(\.\d{0,2})?$/)) {
      this.setState(() => ({ [id]: amount }));
      this.debounce(this.setupLocalStorageSave, 2600)();
    }
  }
  onAccordionClick = (e) => {
    e.preventDefault();
    this.setState(prevState => ({
      accordionOpen: !prevState.accordionOpen
    }));
  }
  onSignupComplete = () => {
    this.setState({
      signupSuccess: false
    });
    window.localStorage.clear();
    window.location.reload();
  }
  onInputChange = ({ target }) => {
    this.setState(() => ({ [target.id]: target.value }));
    this.debounce(this.setupLocalStorageSave, 2600)();
    if (!!this.state.formError) {
      this.checkErrorState();
    }
  }
  onDateChange = (dateString) => {
    const eventDate = new Date(dateString);
    if (eventDate) {
      this.setState(() => ({ eventDate: eventDate.toJSON() }));
      this.debounce(this.setupLocalStorageSave, 2600)();
    }
  }
  onEmailBlur = (e) => {
    const email = e.target.value;
    // starts with digit, any # of digits, allows suffix of one period and 0-2 digits, nothing after
    if (email !== '' && !email.match(/[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/)) {
      this.setState(() => ({
        emailError: 'Oops! Double-check that your email is valid.'
      }));
    } else {
      this.setState(() => ({
        emailError: ''
      }));
    }
  }
  onPhoneBlur = ({ target }) => {
    const phone = target.value;
    // starts with digit, any # of digits, allows suffix of one period and 0-2 digits, nothing after
    if (phone !== '' && !phone.match(/^\+?\d{1,3}?[- .]?\(?(?:\d{2,3})\)?[- .]?\d\d\d[- .]?\d\d\d\d$/)) {
      this.setState(() => ({
        phoneError: 'Double-check, that phone number doesn\'t look valid.'
      }));
    } else {
      this.setState(() => ({
        phoneError: ''
      }));
    }
  }
  onSubmit = (e) => {
    e.preventDefault();
    // perhaps this should be a part of state
    return this.checkErrorState()
      .then((noErrors) => {
        // name, email, eventDate, referralsource required
        if (noErrors) {
          this.setState(() => ({
            formError: ''
          }));
          const inputs = this.flattenedInputs
            .filter(({ id }) => !['drawer', 'row', 'submit'].includes(id))
            .map(({ id, label, type, orphan }) => ({ id: orphan ? label : id, type, value: this.state[id] }));
          const inquiry = formatFormInput({ id: this.state.id, inputs });
          // return console.log(JSON.stringify({ inquiry, userID: this.props.userID }, null, 2));
          return this.startAddInquiry({ inquiry, userID: this.props.userID });
        }
        throw 'Please make sure to fill out the required fields!';
      })
      .then(() => {
        const defaultValues = this.getDefaultValues();
        this.setState(() => ({
          id: '',
          ...defaultValues,
          signupSuccess: true
        }));
      })
      .catch((formError) => {
        // set formError state
        this.setState(() => ({ formError }));
      });
  }
  getDefaultValues = () => {
    const values = this.flattenedInputs
      .reduce((obj, curr) => {
        const { id } = curr;
        // don't create state for the entry 'submit'
        if (id === 'submit') return obj;
        obj[id] = curr.value;
        return obj;
      }, {});
    return values;
  }
  setupLocalStorageSave = () => {
    // strip off all props from state that are not user input
    const { formID } = this.props;
    if (!formID) {
      return console.warn('cannot save to localStorage without a formID');
    }
    const {
      id,
      inputs,
      formError,
      emailError,
      phoneError,
      userID,
      signupSuccess,
      accordionOpen,
      signupSuccessButton,
      signupSuccessHeading,
      signupSuccessMessage,
      windowWidth,
      ...rest
    } = this.state;
    const data = Object.keys(rest).reduce((obj, curr) => {
      // set obj[curr] to 1 if it doesn't exist, or add 1 if it does
      if (rest[curr]) {
        obj[curr] = rest[curr];
      }
      return obj;
    }, {});
    window.localStorage.setItem(formID, JSON.stringify(data));
  }
  updateWindowDimensions = () => {
    this.setState({ windowWidth: window.innerWidth });
  }
  // https://davidwalsh.name/javascript-debounce-function
  // Returns a function, that, as long as it continues to be invoked, will not
  // be triggered. The function will be called after it stops being called for
  // N milliseconds. If `immediate` is passed, trigger the function on the
  // leading edge, instead of the trailing.
  debounce = (func, wait, immediate) => {
    this.saveTimeout = null;
    return (...args) => {
      const later = function afterStatisEndsFunction() {
        this.saveTimeout = null;
        if (!immediate) func.call(args);
      };
      const callNow = testing || (immediate && !this.saveTimeout);
      clearTimeout(this.saveTimeout);
      this.saveTimeout = setTimeout(later, wait);
      if (callNow) func.call(args);
    };
  }
  // TODO: make this return an array of errored input ids?
  // otherwise date input fails to update when moving from error to non-error
  checkErrorState = () => new Promise((res, rej) => {
    const required = this.flattenedInputs
      // only inputs that are required
      .filter(it => it.required)
      // only the name of the input
      .map(it => it.id);
    // check all entries in required against state to see if they're empty strings or undefined
    const remaining = required.filter(prop => ['', undefined].includes(this.state[prop]));
    const noErrors = remaining.length === 0;
    let formError = '';
    // there are errors because of empty required fields
    if (!noErrors) {
      formError = 'Please make sure to fill out the required fields!';
    }
    this.setState(
      () => ({ formError }),
      () => res(noErrors)
    );
  })
  startAddInquiry = ({ inquiry, userID }) => {
    const { id: inquiryID, ...formattedInquiry } = inquiry;
    const updates = { userID, ...formattedInquiry };
    const userInquiryLocation = inquiriesPath({ uid: userID, inquiryID });
    return database.ref(userInquiryLocation).update(updates);
  };
  render() {
    // this getStyles gets object with default || custom key value pairs
    const style = getCSS(getStyles(this.props.styles));
    const { state } = this;
    const flattenedInputsWithState = this.flattenedInputs.map((input) => {
      const currState = state[input.id];
      if (currState) { return ({ ...input, value: currState }); }
      return input;
    });
    const inputComponents = getInputConfig({
      eventDate: state.eventDate,
      formError: state.formError,
      inputs: flattenedInputsWithState,
      onAmountChange: this.onAmountChange,
      onDateChange: this.onDateChange,
      onEmailBlur: this.onEmailBlur,
      onInputChange: this.onInputChange,
      onPhoneBlur: this.onPhoneBlur,
      windowWidth: state.windowWidth
    });
    const renderedInputs = getRenderedComponents({
      accordionOpen: state.accordionOpen,
      inputComponents,
      onAccordionClick: this.onAccordionClick,
      renormalizedInputs: state.inputs,
      style
    });
    const errorClasses = [
      ...(state.formError ? ['F__E--form'] : []),
      ...(state.emailError ? ['F__E--email'] : []),
      ...(state.phoneError ? ['F__E--phone'] : [])
    ];
    return (
      <Signup
        {...state}
        classes={this.props.classes}
        errorClasses={errorClasses}
        handleDrag={this.handleDrag}
        inputs={this.state.inputs}
        renderedInputs={renderedInputs}
        flattenedInputs={flattenedInputsWithState}
        onAccordionClick={this.onAccordionClick}
        onAmountChange={this.onAmountChange}
        onDateChange={this.onDateChange}
        onEmailBlur={this.onEmailBlur}
        onPhoneBlur={this.onPhoneBlur}
        onInputChange={this.onInputChange}
        onSignupComplete={this.onSignupComplete}
        onSubmit={this.onSubmit}
        style={style}
      />
    );
  }
}
SignupContainer.propTypes = {
  classes: PropTypes.string,
  formID: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.string
  ]),
  inputs: PropTypes.array,
  signupSuccessButton: PropTypes.string,
  signupSuccessHeading: PropTypes.string,
  signupSuccessMessage: PropTypes.string,
  styles: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.object
  ]),
  userID: PropTypes.string.isRequired
};
SignupContainer.defaultProps = {
  classes: '',
  formID: false,
  inputs: FORM_ORDER.BASE,
  signupSuccessButton: 'Okay!',
  signupSuccessHeading: 'Thank you!',
  signupSuccessMessage: 'I\'ll be back in touch soon.',
  styles: false
};

export default SignupContainer;
